import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

declare var cordova;
@Component({
  templateUrl: 'usbTest.html'
})
export class USBTestPage {
  usbPlugin: any;
  devices: any = [];

  constructor(public navCtrl: NavController) {
  }

  getUsbDevices() {
    try {
      this.usbPlugin = cordova.chrome.usb;
      console.log(this.usbPlugin);
      this.usbPlugin.getDevices({}, function(found_devices) {
        console.log(found_devices);
        if (found_devices) {
          for (let device of found_devices) {
            console.log(device);
            this.devices.push(device);
          }
        }
      }, function(err) {
        console.log(err);
      });

    } catch (err) {
      console.log(err);
    };
  }

}



