import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { DefaultApi} from '../../api/api'



@Component({
  templateUrl: 'networkTest.html'
})
export class NetworkTestPage {
  private small:JSON[] =  [];
  private medium:JSON[] = [];
  private large:JSON[] = [];
  private results:any;
  constructor(public navCtrl: NavController,
      private api: DefaultApi ) {

  }


  public downloadSmall(){
    console.log("### downloadSmall clicked");
    this.api.getSmall().subscribe((result)=>{
      if(result){
          this.small.push(result);
      }else{
        console.log("Fehler");
      }
    });
  };
  public downloadMedium(){
    this.api.getMedium().subscribe((result)=>{
      if(result){
          this.medium.push(result);
      }else{
        console.log("Fehler");
      }
    });
  };
  public downloadLarge(){
    this.api.getLarge().subscribe((result)=>{
      if(result){
          this.large.push(result);
      }else{
        console.log("Fehler");
      }
    });
  };
}



