import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
//Pages
import { HomePage } from '../pages/home/home';
import { DbTestPage} from '../pages/dbTest/dbTest';
import { USBTestPage} from '../pages/usbTest/usbTest';
import { NetworkTestPage} from '../pages/networkTest/networkTest';
import { NativeTestPage} from '../pages/nativeTest/nativeTest';
import { PerformanceTestPage} from '../pages/performanceTest/performanceTest';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { IonicStorageModule } from '@ionic/storage';
import { DefaultApi} from '../api/DefaultApi';
import { HttpModule } from '@angular/http';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    DbTestPage,
    USBTestPage,
    NetworkTestPage,
    NativeTestPage,
    PerformanceTestPage
  ],
  imports: [
    BrowserModule,
    HttpModule, 
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    DbTestPage,
    USBTestPage,
    NetworkTestPage,
    NativeTestPage,
    PerformanceTestPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    DefaultApi,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



